import './style.css'
import * as THREE from 'three'
import {FBXLoader} from 'three/examples/jsm/loaders/FBXLoader'
import {OBJLoader} from 'three/examples/jsm/loaders/OBJLoader'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { LoadingManager } from 'three'
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()
const textureLoader = new THREE.TextureLoader(LoadingManager)
// const texture = textureLoader.load("https://play.tailwindcss.com/img/grid.svg")
// scene.background = new THREE.Color("white")
// new RGBELoader().load("https://play.tailwindcss.com/img/grid.svg", function(texture) {
//     texture.mapping = THREE.EquirectangularReflectionMapping;
//     scene.background = texture;
//     scene.environment = texture;
// })

// Object
const geometry = new THREE.BoxGeometry(2335, 0.2, 2335)
const material = new THREE.MeshBasicMaterial({ color: "orange" })
const mesh = new THREE.Mesh(geometry, material)
// scene.add(mesh)
mesh.position.set(0,-2,0)

// Light
const light = new THREE.AmbientLight( 0x949494, 0.8 ); // soft white light
scene.add( light );
const pointLight = new THREE.PointLight(0x606060, 2)
pointLight.position.set(1, 30, 20)
scene.add(pointLight)
const pointLight2 = new THREE.PointLight(0x808080)
pointLight2.position.set(-140, 20, -85)
scene.add(pointLight2)


// fbx loader
var fbxLoader = new FBXLoader()

var mixer, action, globalObject;
let modelReady = false;

fbxLoader.load(
    'stretch.fbx',
    function ( object ) {

        //object.material = new THREE.MeshLambertMaterial({color: 0xffffff, vertexColors: THREE.FaceColors});
        object.material = new THREE.MeshStandardMaterial({
            color: 0x000000,
            roughness: 0.5,
            metalness: 0.1,
            flatShading: true,
            // wireframe: true,
            // thickness: 0.01,
            // transparent: true,
            // side: DoubleSide,
            // transmission: 0.95
        });
      

        object.position.set(0, -2, 0)
        object.scale.set(12, 12, 12)
        globalObject = object.position;

        mixer = new THREE.AnimationMixer(object);
        action = mixer.clipAction(object.animations[0])
        action.play()

        scene.add( object );

        modelReady = true;
	},
    function ( xhr ) {

		console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded' );

	},
	function ( error ) {

		console.log( error );

	},
)

// helper
// const helper = new THREE.AxesHelper(3)
// scene.add(helper)

// Sizes
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

// Camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.001, 1000000)
// camera.position.z = 15
// camera.position.y = 14
scene.add(camera)

// controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true
controls.maxPolarAngle = 1.3    
controls.minPolarAngle = 0.9
controls.maxDistance = 20
controls.minDistance = 15
controls.enablePan = false
controls.enableZoom = false
controls.target.set(0,11,0)

controls.enableDamping = true
// controls.minAzimuthAngle = - 0.4; // radians
// controls.maxAzimuthAngle = 0; // radians

// mouse
window.addEventListener('mousemove', mousemove)
const mouse = new THREE.Vector2();

function mousemove ({ x, y }) {
    mouse.x = (x / window.innerWidth) - 0.5;
    mouse.y = (y / window.innerHeight) - 0.5;
}



// resize event
window.addEventListener('resize', onWindowResize, false)
function onWindowResize() {
    camera.aspect = window.innerWidth / window.innerHeight
    camera.updateProjectionMatrix()
    renderer.setSize(window.innerWidth, window.innerHeight)
    renderer.render()
}

// Renderer
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true,
})
renderer.setSize(sizes.width, sizes.height)
renderer.shadowMap.enabled = true;


const clock = new THREE.Clock()

// animation
const tick = () => {

    if (modelReady) mixer.update(clock.getDelta())

    // camera.position.x = ((mouse.x * 30) - camera.position.x);
    // camera.position.z = ((mouse.y * -20) - camera.position.y);

    controls.update()

    renderer.render(scene, camera)

    window.requestAnimationFrame(tick)
}

tick()